.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0; }

*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0; }

ul {
  list-style: none; }

img {
  display: block;
  padding: 0;
  margin: 0; }

.page__body {
  font-family: "Montserrat", sans-serif;
  position: relative; }

.page-header {
  display: flex;
  justify-content: space-around;
  align-items: stretch;
  padding-top: 39px;
  padding-bottom: 38px; }

.page-header__logo {
  flex-basis: 50%; }

.main-nav__list {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%; }

.page-main__wrapper {
  padding: 0 12.39583%; }

.page-main__intro {
  background-image: url("../img/bg-intro-desktop@1x.png");
  background-color: #f2f3f4;
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 860px; }

.page-main__places-wrap {
  margin-top: 199px; }

.page-main__places {
  margin-left: 9.48753%; }

.page-main__country-card {
  margin-top: 56px; }

.page-main__tab-details {
  margin-top: 95px;
  margin-bottom: 240px; }

.page-main__form {
  position: relative;
  background: url("../img/bg-sea-horizon-desktop@1x.png");
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: auto;
  min-height: 455px;
  margin-bottom: 170px; }

.card-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr; }

.tabs__menu {
  display: flex;
  justify-content: space-around;
  margin-bottom: 95px; }

.tabs__link {
  text-decoration: none; }

.tab-details {
  position: relative; }

.tab-details__item {
  display: flex;
  justify-content: flex-start; }
  .tab-details__item:not(:first-child) {
    display: none; }

.tab-details__wrapper {
  margin-right: 100px;
  margin-top: 15px; }

.tab-details__title {
  margin-bottom: 53px; }

.tab-details__description {
  width: 370px; }

.tab-details__review-wrap {
  max-width: 382px; }

.tab-details__button {
  position: absolute;
  top: 505px;
  left: 0;
  transform: translateY(-50%); }

.review {
  position: absolute;
  top: 114px;
  left: 655px;
  width: 382px;
  background-color: #ffffff;
  padding: 30px; }

.review__title {
  margin-bottom: 32px; }

.review__item:not(:first-child) {
  display: none; }

.review__text {
  margin-bottom: 35px; }

.review__inner-wrap {
  display: flex;
  justify-content: flex-start;
  align-items: center; }

.review__author {
  margin-left: 16px; }

.prices {
  display: grid;
  grid-template-columns: 1fr 1fr;
  -moz-column-gap: 111px;
       column-gap: 111px; }

.prices__offer {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 2;
  margin-bottom: 138px; }

.prices__wrap {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2; }

.offer__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  -moz-column-gap: 30px;
       column-gap: 30px; }

.form {
  position: absolute;
  top: 50%;
  left: 514px;
  transform: translateY(-50%);
  background-color: #ffffff;
  padding: 50px 49px 49px 50px;
  max-width: 656px;
  box-shadow: 0 4px 30px rgba(41, 54, 66, 0.1);
  border-radius: 5px; }

.form__title {
  font-family: inherit;
  font-size: 25px;
  line-height: 32px;
  font-weight: 600;
  color: #141517;
  margin-bottom: 11px;
  text-align: center; }

.form__text {
  font-family: inherit;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  color: #141517;
  margin-bottom: 22px;
  text-align: center; }

.form__question {
  display: flex;
  flex-direction: column; }

.form__input {
  width: 267px;
  height: 50px;
  margin-bottom: 25px; }
  .form__input--email {
    margin-left: 18px; }

.form__submit {
  justify-self: center;
  padding: 18px 39px 17px 40px;
  margin-right: auto;
  margin-left: auto;
  font-family: inherit;
  line-height: 15px;
  font-weight: bold;
  text-transform: uppercase;
  background-color: #fe7865;
  color: #ffffff;
  border: none; }

.page-footer {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  padding: 0 384px 75px 238px; }

.next-travel__list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr; }

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.modal__buy {
  display: none; }

.modal__buy-wrap {
  position: relative;
  display: flex;
  width: 728px;
  height: auto;
  background-color: #fbf8f8; }

.modal__buy-close {
  position: absolute;
  top: 20px;
  right: 22px;
  width: 22px;
  height: 22px; }

.modal__buy-inner-wrap {
  padding: 72px 61px 89px 59px;
  width: 100%;
  text-align: center; }

.modal__buy-title {
  margin-bottom: 52px; }

.modal__buy-input-wrap {
  margin-bottom: 20px; }

.modal__buy-input {
  width: 270px;
  min-height: 50px; }

.modal__buy-submit {
  font-family: inherit;
  font-size: 12px;
  line-height: 15px;
  font-weight: 700;
  text-transform: uppercase;
  padding: 18px 39px 17px 40px;
  background-color: #fe7865;
  color: #ffffff;
  border: none; }

.modal__message {
  display: none;
  font-family: inherit;
  font-size: 25px;
  line-height: 43px;
  font-weight: 500;
  text-align: center;
  width: 728px;
  padding: 179px 164px 179px 165px;
  background-color: #fbf8f8;
  color: #48494d; }

.modal__message-close {
  position: absolute;
  top: 20px;
  right: 22px;
  width: 22px;
  height: 22px; }
